import React from 'react'
// import { Link } from 'react-router-dom'

export default function Navbar(props) {
  return (
    <div>
      <nav className= {`navbar navbar-expand-lg navbar-dark bg-${props.mode}`}>
        <div className="container-fluid">
          {/* <Link className="navbar-brand" to="/">{props.name}</Link> */}
           {/* eslint-disable-next-line */}
          <a className="navbar-brand" href="#">{props.name}</a>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                {/* <Link className="nav-link active" aria-current="page" to="/">Home</Link> */}
                {/* eslint-disable-next-line */}
                <a className="nav-link active" aria-current="page" href="#">Home</a>
              </li>
              <li className="nav-item">
                {/* <Link className="nav-link" to="/about">About Us</Link> */}
                {/* eslint-disable-next-line */}
                <a className="nav-link disabled" href="#">About Us</a>
              </li>
            </ul>
            <div className="form-check form-switch text-light">
              <input className={`form-check-input bg-${props.mode}`} onClick={props.styling} type="checkbox" id="flexSwitchCheckDefault" />
              <label className="form-check-label" htmlFor="flexSwitchCheckDefault">{props.myText}</label>
            </div>
          </div>
        </div>
      </nav>
    </div>
  )
}
