import './App.css';
import Navbar from './components/Navbar';
import Converter from './components/Converter';
// import About from './components/About';
import React, { useState } from 'react';
import Alert from './components/Alert';
// import {
//   BrowserRouter,
//   Routes,
//   Route,
// } from "react-router-dom";

function App() {
  // eslint-disable-next-line
  const [col, setCol] = useState("dark");
  const [mode, setMode] = useState("primary");
  const [myText, setMyText] = useState("Enable Dark Mode");
  const [alert, setAlert] = useState(null);

  const styleIt = () => {
    if (mode === "primary") {
      setMode("dark");
      setMyText("Enable Light Mode");
      document.body.style.backgroundColor = '#091a32';
      document.body.style.color = 'white';
      showAlert("Dark mode has been enabeled!", "success");
    }
    else {
      setMode("primary");
      setMyText("Enable Dark Mode");
      document.body.style.backgroundColor = 'white';
      document.body.style.color = 'black';
      showAlert("Light mode has been enabeled!", "success");
    }
  }

  const showAlert = (message, type) => {
    setAlert({
      msg: message,
      typ: type
    })
    setTimeout(() => {
      setAlert(null);
    }, 3000);
  }

  return (
    <div className="App">
      {/* <BrowserRouter>
        <Navbar name="TextUtils" mode={mode} styling={styleIt} myText={myText} col={col}></Navbar>
        <Alert alert={alert}></Alert>
        <div className="container my-3">
          <Routes>
            <Route exact path='/' element={<Converter heading="Write a text here to convert it in UPPERCASE/lowercase:" mode={mode} showAlert={showAlert} />}></Route>
            <Route exact path='/about' element={< About mode={mode} />}></Route>
          </Routes>
        </div>
      </BrowserRouter> */}
      <Navbar name="TextUtils" mode={mode} styling={styleIt} myText={myText} col={col}></Navbar>
      <Alert alert={alert}></Alert>
      <div className="container">
        <Converter heading="Write a text here to convert it in UPPERCASE/lowercase:" mode={mode} showAlert={showAlert} />
      </div>
      {/* < About mode={mode} /> */}
    </div>
  );
}

export default App;
